import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus, faMinusCircle } from '@fortawesome/free-solid-svg-icons'
import byteSize from 'byte-size'
import { PdfPreview } from './PdfViewer'

import classes from './FileUpload.module.scss'

const preview = file => {
  return (
    <React.Fragment>
      {file.type.includes('image') && <img src={URL.createObjectURL(file)} />}
      {file.type === 'application/pdf' && <PdfPreview file={file} />}
    </React.Fragment>
  )
}

export default class FileUpload extends React.Component {
  constructor (props) {
    super(props)
    this.dialogRef = React.createRef()

    this.state = {
      files: props.files || []
    }
    this.handleOnClickUpload = this.handleOnClickUpload.bind(this)
    this.handleOnChangeFile = this.handleOnChangeFile.bind(this)
    this.handleOnRemoveItem = this.handleOnRemoveItem.bind(this)
  }

  handleOnClickUpload (event) {
    event.stopPropagation()
    event.preventDefault()
    this.dialogRef.current.click()
  }

  handleOnChangeFile (event) {
    event.stopPropagation()
    event.preventDefault()
    const file = event.target.files[0]

    if (!file) return

    const files = [...this.state.files, {file, key: `${file.name}${Date.now()}`}]

    this.setState({files})

    this.props.onChange(files.reduce((a, c) => ([...a, c.file]), []))
  }

  handleOnRemoveItem (index) {
    const files = this.state.files.filter((item, _index) => index !== _index)

    this.setState({files})
    this.props.onChange(files.reduce((a, c) => ([...a, c.file]), []))
  }

  render () {
    return (
      <React.Fragment>
        <div className={classes.item} key={this.props.name}>
          <div className={classes['item-header']}>
            <div className={classes['item-name']}>{this.props.text}</div>
            {this.state.files.length === 0 && (
              <div className={classes['item-action']}>
                <button onClick={this.handleOnClickUpload}>Upload</button>
              </div>
            )}
          </div>

          {this.state.files.length > 0 && (
            <div className={classes.files}>
              {this.state.files.map((item, index) => (
                <div className={classes.file} key={`file-${this.props.name}-${item.key}`}>
                  <div className={classes.preview}>
                    <React.Fragment>
                      {preview(item.file)}
                      <FontAwesomeIcon
                        icon={faMinusCircle}
                        className={classes.remove}
                        onClick={() => this.handleOnRemoveItem(index)}
                      />
                    </React.Fragment>
                  </div>
                  <div className={classes['file-name']}>
                    <span className={classes.text}>{item.file.name}</span>
                    <span className={classes.tooltip}>{item.file.name}</span>
                  </div>

                  <div className={classes['file-size']}>
                    {byteSize(item.file.size).value} {byteSize(item.file.size).unit}
                  </div>
                </div>
              ))}

              <div className={classes.file} key={`file-${this.props.name}-add`}>
                <div className={classes.preview} onClick={this.handleOnClickUpload}>
                  <FontAwesomeIcon icon={faPlus} />
                </div>
              </div>
            </div>
          )}
        </div>

        <input
          id='file-dialog'
          type='file'
          ref={this.dialogRef}
          style={{ display: 'none' }}
          accept={(this.props.accepts || []).join(',')}
          onChange={this.handleOnChangeFile}
        />
      </React.Fragment>
    )
  }
}
