const initialState = {
  facilityGroupingEditable: []
}

export default function facilityGroupingEditable (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_FACILITY_GROUPING_EDITABLE':
      return {
        ...state,
        facilityGroupingEditable: payload
      }

    default:
      return state
  }
}
