const dateNow = new Date().toString();
const initialState = {
  dentalClinicForm: {
    clinicName: '', // required
    mobile: '', // required
    email: '',
    mainCoordinator: '', // required
    checkPayee: '', // required
    unitFlrBldg: '',
    bldgName: '',
    street: '', // required
    subdivision: '', // required
    region: '', // required
    district: '', // required
    barangay: '', // required
    city: '', // required
    zipCode: null, // required
    clinicSchedule: '', // required
    natureOfPractice: '', // required
    internalRemarks: '', // Required
    externalRemarks: '', // Required
    dentalAssociates: [],
    affiliationInfos: []
  },

  dentistProfile: {
    firstName: '',
    middleName: '',
    lastName: '',
    suffix: '',
    birthDate: dateNow,
    civilStatus: '',
    gender: '',
    emailAddress: '',
    contactNumber: '',
    mainSpecialization: '', // required
    majorSpecialization: '', // required
    otherSpecialization: '', // required
    subSpecialization: '', // required
    pdaNumber: '',
    prcNumber: '',
    prcExpiration: dateNow, // required
    philhealthNumber: null,
    philhealthExpiration: dateNow,
    tinStatus: '', // required
    tinNumber: '',
    taxRate: '',
    nonVatable: ''
  }
}

export default function dentalClinicAffilForm (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_DENTAL_CLINIC_AFFIL_FORM':
      return {
        ...state,
        dentalClinicForm: payload
      }

    case 'SET_DENTIST_PROFILE':
      return {
        ...state,
        dentistProfile: payload

      }

    default:
      return state
  }
}
