import React from 'react'
import { Link } from 'react-router-dom'
import { Scrollbars } from 'react-custom-scrollbars'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useSelector, useDispatch } from 'react-redux'
import { setSidebarItems } from 'Pages/Protected/Store/actions'
// import { Popover } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
// CSS Classes
import classes from './SideBarComponent.module.scss'

let dispatch = null

const SideBarComponent = () => {
  const [anchorEl, setAnchorEl] = React.useState(null)

  dispatch = useDispatch()
  const items = useSelector(state => state.sidebar)

  const handleClick = event => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const renderNavigation = item => {
    if (!item.type || (item.type && item.type) === 'link') {
      return (
        <Link id={`${item.label}`} to={item.to} className={classes.item} key={item.to}>
          <div className={classes.circle}>
            <FontAwesomeIcon icon={item.icon} className={classes.icon} />
          </div>
          {item.label ? <span className={classes.text}>{item.label}</span> : ''}
        </Link>
      )
    } else {
      return (
        <React.Fragment key={'pop-up-group'}>
          <Button className={classes.item} onClick={handleClick}>
            <div className={classes.circle}>
              <FontAwesomeIcon icon='chart-bar' className={classes.icon} />
            </div>
            {item.label ? <span className={classes.text}>{item.label}</span> : ''}
          </Button>
          <Menu
            className={classes.item}
            id={`id`}
            open={!!anchorEl}
            anchorEl={anchorEl}
            onClose={handleClose}
            getContentAnchorEl={null}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right'
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left'
            }}
            keepmounted='true'
          >
            {item.child &&
              item.child.map((link, index) => (
                <Link
                  id={`popup-item-${index}`}
                  to={link.to}
                  key={`popup-item-${index}`}
                  style={{ textDecoration: 'none', color: '#212d71' }}
                >
                  <MenuItem>{link.label ? link.label : ''}</MenuItem>
                </Link>
              ))}
          </Menu>
        </React.Fragment>
      )
    }
  }

  return (
    <div className={classes.root}>
      <Scrollbars className={classes.menu}>
        {items.map(item => renderNavigation(item))}

        <Link id={'Settings'} to='/' className={classes.item}>
          <div className={classes.circle}>
            <FontAwesomeIcon icon='cogs' className={classes.icon} />
          </div>
          <span className={classes.text}>Settings</span>
        </Link>

        <Link id={'Open'} to='/' className={classes.item}>
          <div className={classes.circle}>
            <FontAwesomeIcon icon='book-open' className={classes.icon} />
          </div>
          {/* <span className={classes.text}>Settings</span> */}
        </Link>
      </Scrollbars>
    </div>
  )
}

const SideBarService = {
  setItems: items => {
    dispatch(setSidebarItems(items))
  }
}

export { SideBarComponent, SideBarService }
