const initialState = {
  pfMultiplierEditable: []
}

export default function pfMultiplierEditable (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_PF_MULTIPLIER_EDITABLE':
      return {
        ...state,
        pfMultiplierEditable: payload
      }

    default:
      return state
  }
}
