let initialState = {
  name: '', // required
  bedCapacity: '', // required
  email: '', // required
  years: '', // required
  months: '', // required
  dohNumber: '',
  dohLicenseType: '',
  dohLicenseExp: '',
  philhealthNumber: '',
  philhealthExpiration: '',
  payee: '', // required
  classification: '',
  tinStatusValue: '', // required
  tinStatus: '', // required
  vatTaxRate: '',
  processType: '', // required
  unitFlrBldg: '', // required
  bldgName: '',
  street: '', // required
  subdivision: '', // required
  region: '', // required
  district: '', // required
  barangay: '', // required
  city: '', // required
  zipCode: '', // required
  cName: '', // required
  cDesignation: '', // required
  cDepartment: '', // required
  cEmail: '', // required
  cNumber: '', // required

  affiliateGroup: '',
  internalRemarks: '', // required,
  externalRemarks: ''
}

const hospitalAffiliationReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'set_name':
      return { ...state, ...action.payload }

    default:
      return state
  }
}

export default hospitalAffiliationReducer
