export default function GenerateFormData (data) {
  const formData = new FormData()
  Object.keys(data).map((item, index) => {
    if (Array.isArray(data[item])) {
      data[item].map(_item => {
        let value = _item

        if (typeof _item === 'object' && !(_item instanceof File)) {
          value = JSON.stringify(_item)
        }

        formData.append(`${item}[]`, value)
      })
    } else {
      if ((data[item] instanceof File)) {
        formData.append(`${item}[]`, data[item])
      } else {
        const value = typeof data[item] === 'object' ? JSON.stringify(data[item]) : data[item]
        formData.append(item, value)
      }
    }
  })

  return formData
}
