import React from 'react'
import { Document, Page, pdfjs } from 'react-pdf'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

const PdfPreview = ({ file }) => {
  return (
    <Document
      file={file}
      loading={<span style={{ color: 'black', fontSize: '12px' }}>Generating Preview</span>}
      noData={<span style={{ color: 'black', fontSize: '12px' }}>Invalid PDF</span>}
    >
      <Page
        pageNumber={1}
        scale={1}
        height={120}
        loading={<span style={{ color: 'black', fontSize: '12px' }}>Generating Preview</span>}
      />
    </Document>
  )
}

export { PdfPreview }
