import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import FileUpload from 'SharedComponents/FileUpload/FileUpload'

import OtherServices from 'Services/Api/Partner/OtherServices'
import classes from './FileUpload.module.scss'

export default function FileUploadPage (props) {
  const [items, setItems] = React.useState([
    { name: 'doctors_resume', text: `Doctor's Resume` },
    { name: 'copy_of_certificate', text: `Copy of Certificate` }
  ])

  const handleOnChange = ({ index, files }) => {
    setItems(state =>
      state.map((item, _index) => {
        if (_index === index) item.files = files
        return item
      })
    )
  }

  const handleOnSend = () => {
    let data = {}
    for (let item of items) {
      data[item.name] = item.files
    }

    OtherServices.sendFrom({ data }).then(res => console.log(res))
  }

  return (
    <div className={classes.root}>
      <div className={classes.header}>
        <Typography variant='subtitle2'>Document Description</Typography>
      </div>

      <div className={classes.items}>
        {items.map((item, index) => (
          <FileUpload key={item.name} {...item} onChange={files => handleOnChange({ index, files })} />
        ))}
      </div>

      <Button onClick={handleOnSend}>Send</Button>
    </div>
  )
}
