export const setBreadcrumbsItems = payload => {
  return {
    type: 'SET_BREADCRUMBS_ITEMS',
    payload
  }
}

export const setSelectedBreadcrumbs = payload => {
  return {
    type: 'SET_SELECTED_BREADCRUMBS',
    payload
  }
}

export const setSelectedBreadCrumbsWithoutHome = payload => {
  return {
    type: 'SET_SELECTED_BREADCRUMBS_WIHOUT_HOME',
    payload
  }
}

export const setManualBreadCrumbs = payload => {
  return {
    type: 'SET_MANUAL_BREAD_CRUMBS',
    payload
  }
}
